import React from 'react';
import st from './admin.module.scss';
import { Collapse } from 'antd';
import HeaderBlockText from '../../components/HeaderBlock/HeaderBlockText';
import {
  CreateAbout,
  CreateNews,
  CreatePortfolio,
  NewsPanel,
  PortfolioPanel,
  AboutPanel,
} from '../../components/AdminComponents';

const { Panel } = Collapse;

export const Admin = () => {
  return (
    <div className="wrapper">
      <div className={st.container}>
        <Collapse bordered={false} className={st.collapse}>
          <HeaderBlockText></HeaderBlockText>
          <Panel header="" key="1">
            <CreateNews> </CreateNews>
          </Panel>
          <Panel header="" key="2">
            <CreatePortfolio>  </CreatePortfolio>
          </Panel>
          <Panel header=" " key="3">
            <CreateAbout> </CreateAbout>
          </Panel>
        </Collapse>
        <Collapse bordered={false} className={st.collapse}>
          <HeaderBlockText> / </HeaderBlockText>
          <Panel header="" key="1">
            <NewsPanel />
          </Panel>
          <Panel header="" key="2">
            <PortfolioPanel />
          </Panel>
          <Panel header=" " key="3">
            <AboutPanel />
          </Panel>
        </Collapse>
      </div>
    </div>
  );
};
